import { queryArticleCommentService } from '../service';
import { mapState } from 'vuex';
export default {
  props: ["article"],
  data() {
      return {
          comments: [],
      }
  },
  async mounted() {
      const { data } = await queryArticleCommentService(this.article.slug);
      this.comments = data.comments;
  },
    computed: {
        ...mapState(['user'])
    }
};
